<style type="text/css" lang="scss">
	@import "../../scss/variables";
	.page-notFound{
		text-align: center;
		padding: 0.27rem;
		.page-notFound-content{
			height: 1.62rem;
			margin-top: 0.3rem;
			border: 1px solid #ddd;
			padding: 0.46rem 0.07rem 0.26rem 0.07rem;
			box-shadow: 0px 0px 20px #97BEFD;
			img{
				width: 1.78rem;
				height: 0.35rem;
			}
			span{
				font-size: $font-size-middle;
				color: #888;
			}
			.page-notFound-content-text{
				margin-top: 0.05rem;
			}
			.page-notFound-content-btn{
				margin-top: 0.4rem;
			}
		}
	}
</style>
<template>
	<div class="page-notFound">
		<div class="page-notFound-content">
			<div>
				<div>
					<img src="../../assets/404.png" />
				</div>
				<div class="page-notFound-content-text">
					<span>很抱歉，页面不小心迷路了</span>
				</div>
			</div>
			<div class="page-notFound-content-btn">
				<router-link :to="{name: 'czy-homeList-sr', query: {close: '1'}}">
					<button class="am-button tiny-blue">返回首页</button>
				</router-link>
			</div>
		</div>

	</div>
</template>
<script>
export default{
  name: 'not-found'
}
</script>



